import React from "react"
// import ReactMarkdown from "react-markdown"
// import { Prism as SyntaxHighlighter } from "react-syntax-highlighter"
// import { darcula } from "react-syntax-highlighter/dist/esm/styles/prism"
// import remarkGfm from "remark-gfm"
// import rehypeKatex from "rehype-katex"


const MarkdownRenderer = (props) => <div
    className={`flex flex-col w-[768px] mx-auto my-0 px-0 py-24`} {...props}>{props.children}</div>


export interface IPostContentProps {
    html: string
    //children?: React.ReactNode,
}

const PostContent = ({ html }: IPostContentProps) => {

    return (
        <MarkdownRenderer dangerouslySetInnerHTML={{
            __html: html.replaceAll('<h1>', '<h1 class="mb-8 font-extrabold">')
                    .replaceAll('<h2>', '<h2 class="mb-8 font-extrabold">')
                    .replaceAll('<h3>', '<h3 class="mb-8 font-extrabold">')
                    .replaceAll('<hr>', '<hr class="w-full border border-b-0 border-solid border-slate-200">')
                    .replaceAll('<table>', '<table class="border border-solid border-slate-300 border-collapse">')
                    .replaceAll('<thead>', '<thead class="bg-gray-200">')
                    .replaceAll('<tbody>', '<tbody class="">')
                    // .replaceAll('<tr>', '<tr class="border-0 border-b-2 border-solid border-gray-300 p-2">')
                    // .replaceAll('<tr>', '<tr class="border border-solid border-black">')
                    .replaceAll('<th>', '<th class="p-3">')
                    .replaceAll('<td>', '<td class="border border-solid border-slate-300 p-3">')
                    .replaceAll('<p>', '<p class="px-0">')
                    .replaceAll('<blockquote>', '<blockquote class="mx-0 my-8 p-3 border-slate-600 border-0 border-l-2 border-solid bg-gray-100">')
                    .replaceAll('<code class="language-text">', '<code class="bg-gray-100 text-sm text-red-500 rounded p-1 ">')
        }} />

    )
}


PostContent.defaultProps = {}

export default PostContent